<!-- 我的任务头部 -->
<template>
  <van-row style="height:100%">
    <!-- 顶部蓝色框首页 -->
    <van-row type="flex"
             align="center"
             class="dt-title">
      <van-col span="2">
        <van-icon class="dt-back"
                  @click="back"
                  name="arrow-left" />
      </van-col>
      <van-col span="20">
        <p class="dt-home-shouye">任务管理</p>
      </van-col>
    </van-row>
    <van-tabs v-model="active">
      <van-tab v-if="isAdmin"
               :title="'待审核('+totalCount1+')'">
        <orderList :sendForm="form1"
                   :formType="1">
        </orderList>
      </van-tab>
      <van-tab v-if="isAdmin"
               :title="'已审核('+totalCount2+')'">
        <orderList :sendForm="form2"
                   :formType="2">
        </orderList>
      </van-tab>
      <van-tab v-if="!isAdmin"
               :title="'未阅('+totalCount3+')'">
        <orderList :sendForm="form3"
                   :formType="3">
        </orderList>
      </van-tab>
      <van-tab v-if="!isAdmin"
               :title="'已阅('+totalCount4+')'">
        <orderList :sendForm="form4"
                   :formType="4">
        </orderList>
      </van-tab>
    </van-tabs>
  </van-row>
</template>

<script>
//这里可以导入其他文件（比如：组件，工具js，第三方插件js，json文件，图片文件等等）
//例如：import 《组件名称》 from '《组件路径》';
import {
  GetPageContentByConditions, // 任务查询
  GetTaskuser  // 查询用户已阅未阅列表（任务下发记录查询）
} from "@/api/testApi";
import orderList from "@/views/taskManage/taskList";
export default {
  //import引入的组件需要注入到对象中才能使用
  components: { orderList },
  data () {
    //这里存放数据
    return {
      // form: {
      //   // TaskCode: "", // 任务编码
      //   TaskTitle: "",  // 任务名称
      //   TaskStatus: 2,  // 任务状态（1:待审核.2:已审核.3.已驳回,4.已完成)
      //   // SubmitUserId: "",  // 提交人
      //   // ExamineUserId: "", // 审核人
      //   //SendUserId: null, // 下发人id Int
      //   PageSize: 20, // 默认20 Int
      //   PageIndex: 0 // 默认1 Int
      // },
      // 任务管理查询我下发的任务
      form1: { // 待审核
        TaskTitle: "",  // 任务名称
        TaskStatus: 1,  // 任务状态（1:待审核.2:已审核.3.已驳回,4.已完成)
        SubmitUserId: JSON.parse(localStorage.getItem("userInfo")).uid, // 提交人id
        SubBeginDate: new Date(new Date().format('yyyy-MM-ddT00:00:00')),
        SubEndDate: new Date(new Date().format('yyyy-MM-ddT23:59:59')),
        PageSize: 20, // 条数 Int 默认20
        PageIndex: 0, // 页码 Int 默认1
      },
      form2: { // 已审核
        TaskTitle: "",  // 任务名称
        TaskStatuss: [2, 3],  // 任务状态（1:待审核.2:已审核.3.已驳回,4.已完成)
        SubmitUserId: JSON.parse(localStorage.getItem("userInfo")).uid, // 提交人id
        SubBeginDate: new Date(new Date().format('yyyy-MM-ddT00:00:00')),
        SubEndDate: new Date(new Date().format('yyyy-MM-ddT23:59:59')),
        PageSize: 20, // 条数 Int 默认20
        PageIndex: 0, // 页码 Int 默认1
      },
      form3: { // 未阅
        ReceiveUserId: JSON.parse(localStorage.getItem("userInfo")).uid, // 接受人usid
        SendBeginDate: new Date(new Date().format('yyyy-MM-ddT00:00:00')),
        SendEndDate: new Date(new Date().format('yyyy-MM-ddT23:59:59')),
        ReadStatus: 0, // 阅读状态 0.未阅读，1.已阅读
        PageSize: 20, // 条数 Int 默认20
        PageIndex: 0, // 页码 Int 默认1

      },
      form4: { // 已阅
        ReceiveUserId: JSON.parse(localStorage.getItem("userInfo")).uid, // 接受人usid
        SendBeginDate: new Date(new Date().format('yyyy-MM-ddT00:00:00')),
        SendEndDate: new Date(new Date().format('yyyy-MM-ddT23:59:59')),
        ReadStatus: 1, // 阅读状态 0.未阅读，1.已阅读
        PageSize: 20, // 条数 Int 默认20
        PageIndex: 0, // 页码 Int 默认1
      },
      totalCount1: 0, // 数据总数
      totalCount2: 0, // 数据总数
      totalCount3: 0, // 数据总数
      totalCount4: 0, // 数据总数
      isAdmin: true,  // 是否为管理员
      active: 0,//tab状态
    };
  },
  //监听属性 类似于data概念
  computed: {},
  //监控data中的数据变化
  watch: {},
  //生命周期 - 创建完成（可以访问当前this实例）
  created () {
    this.getIsAdmin()
  },
  //生命周期 - 挂载完成（可以访问DOM元素）
  mounted () {
    this.initTotalCount()
    this.active = this.$store.state.active
  },
  beforeCreate () { }, //生命周期 - 创建之前
  beforeMount () { }, //生命周期 - 挂载之前
  beforeUpdate () { }, //生命周期 - 更新之前
  updated () { }, //生命周期 - 更新之后
  beforeDestroy () { }, //生命周期 - 销毁之前
  destroyed () { }, //生命周期 - 销毁完成
  activated () { }, //如果页面有keep-alive缓存功能，这个函数会触发
  //方法集合
  methods: {
    // 工单查询
    initTotalCount () {
      GetPageContentByConditions(this.form1).then(response => {
        this.totalCount1 = response.Data.TotalCount
      })
      GetPageContentByConditions(this.form2).then(response => {
        this.totalCount2 = response.Data.TotalCount
      })
      /**获取用户已阅未阅列表的接口，统一认证信息接口通了再使用 */
      GetTaskuser(this.form3).then(response => {
        this.totalCount3 = response.Data.TotalCount
      })
      GetTaskuser(this.form4).then(response => {
        this.totalCount4 = response.Data.TotalCount
      })
    },

    // 判断用户是否为管理员
    getIsAdmin () {
      if (!localStorage.getItem("roleInfoList")) {
        this.isAdmin = true
        return
      }
      let roleInfoList = JSON.parse(localStorage.getItem("roleInfoList"))
      if (roleInfoList.indexOf("APProleKey") != -1) {
        this.isAdmin = true
      } else {
        this.isAdmin = false
      }
    },
    // 返回首页按钮
    back () {
      this.$store.commit("active", 0)
      history.back(-1);
    },

  },
}
</script>
<style lang='scss' scoped>
//@import url(); 引入公共css类
@import '../../../styles/dimens';
.dt-title {
  width: 100%;
  height: $headerHeight;
  padding-top: $statusBarHeight;
  background-color: #5870f8;
}
.dt-back {
  height: px2rem(14);
  width: px2rem(14);
  margin-left: px2rem(6);
  color: #fff;
  font-size: px2rem(18);
}
.dt-home-shouye {
  font-size: px2rem(17);
  font-family: 'SourceHanSansCN-Medium';
  color: #ecedf2;
}
.van-tabs {
  height: calc(100% - #{$headerHeight} - #{$statusBarHeight});
}
/deep/.van-tabs__content {
  height: calc(100% - #{$thirdHeight});
}
/deep/.van-tab__pane,
.van-tab__pane-wrapper {
  height: 100%;
}
/deep/.van-tabs__line {
  background-color: #5870f8;
}
/deep/.van-tab--active > span {
  color: #5870f8;
}
</style>